b232;\n--color-success-active: #6ba122;\n--color-success-active-hover: #5d9110;\n\n--color-warning-disabled-hover: #ffda46;\n--color-warning-disabled: #ffc734;\n--color-warning: #f1b521;\n--color-warning-hover: #dea406;\n--color-warning-active: #cc9400;\n--color-warning-active-hover: #ba8400;\n\n--color-danger-disabled-hover: #ff5323;\n--color-danger-disabled: #ff4313;\n--color-danger: #f83200;\n--color-danger-hover: #e31f00;\n--color-danger-active: #cd0500;\n--color-danger-active-hover: #b80000;\n\n  --dense-spacing-custom: false;\n  --table-scroll-width: 0;\n}\n  $color-primary-custom: #f86e00;\n  $color-secondary-custom: #f9f9fa;\n  $color-active-custom: #00a79d;\n\n  $useLightTextWithPrimaryColor: true;\n  $useLightTextWithSecondaryColor: false;\n\n  $border-radius: var(--border-radius);\n  $color-critical-custom: #f83200;\n  $color-warning-custom: #f1b521;\n  $color-success-custom: #89c341;\n  $color-info-custom: #3cb6ce;\n\n  $color-primary-disabled: #ff7d18;\n  $color-secondary-disabled: #ffffff;\n  $color-active-disabled: #2db7ad;\n  $color-warning-disabled: #ffc734;\n  $color-danger-disabled: #ff4313;\n  $color-success-disabled: #99d550;\n  $color-info-disabled: #52c7df;\n\n    $logo-dark-custom: $logo-bmc-dark-icon;\n    $logo-helix-dark-custom: $logo-bmc-helix-dark-icon;\n\n    $logo-light-custom: $logo-bmc-light-icon;\n    $logo-helix-light-custom: $logo-bmc-helix-light-icon;\n\n\n  $nav-theme-custom: dark;\n  $font-family-base: var(--font-family);\n  $dense-spacing-custom: false;\n\n  $menu-dropdown-color-custom: darken(#313538, 5%);\n  $menu-selected-color-custom: darken(#313538, 15%);\n  $nav-icon-color: #ffffff;\n\n  $table-scroll-width: var(--table-scroll-width);\n","//\n// ## ADAPT-CSS Variables\n//\n\n// Table of Contents\n//\n// Color system\n// Options\n// Spacing\n// Letter spacing\n// Body\n// Links\n// Paragraphs\n// Grid breakpoints\n// Grid columns\n// Grid containers\n// Animation\n\n// Components\n// Fonts\n// Headings\n// Labels\n// Lists\n// Tree\n// Tables\n// Buttons\n// Forms\n// Dropdowns\n// Z-index master list\n// Navs\n// Navbar\n// Pagination\n// Jumbotron\n// Cards\n// Tooltips\n// Popovers\n// Metatags\n// Badges\n// Modals\n// DockedPanels\n// Sidebar\n// Alerts\n// Toasts\n// Progress bars\n// List group\n// Image thumbnails\n// Uploader\n// Figures\n// Breadcrumbs\n// Carousel\n// Close\n// Code\n// Profile\n// Login\n// Logo\n// Tree view\n// Printing\n// Toasts\n// Busy, Loaders\n// Charts\n// Rating\n\n// RTL\n\n@use \"sass:math\";\n\n$is-mobile: false !default;\n\n$start: left;\n$end: right;\n\n$rtl-mode: false !default;\n\n// HP redefine (need to have in case other team compiles it)\n//these var defined in scss/_variables.scss\n//$color-primary-custom: #f86e00 !default;  // BMC default is Orange\n//$color-secondary-custom: #f9f9fa !default; // BMC default is Gray\n//$color-active-custom: #00a79d !default;  //BMC default is Teal\n\n$nav-theme-custom: dark !default;\n\n$logo-dark-custom: $logo-bmc-dark-icon !default;\n$logo-light-custom: $logo-bmc-light-icon !default;\n$logo-helix-dark-custom: $logo-bmc-helix-dark-icon !default;\n$logo-helix-light-custom: $logo-bmc-helix-light-icon !default;\n\n//\n// ## Color system\n//\n// Brand colors\n$color-primary:   $color-primary-custom !default; //BMC default is Orange\n$color-secondary: $color-secondary-custom !default; //BMC default is Gray\n$color-active:    $color-active-custom !default; //BMC default is Teal\n\n// Status-oriented colors\n$color-info:     $color-info-custom !default;  //BMC default is Blue\n$color-success:  $color-success-custom !default;  //BMC default is Green\n$color-warning:  $color-warning-custom !default;  //BMC default is Yellow\n$color-critical: $color-critical-custom !default;  //BMC default is Red\n$color-danger: $color-critical;  //BMC default is Red\n\n//\n// Use 'color-variant' function for generating color-variants:\n// base, hover, active, active-hover, disabled, disabled-hover, row, row-hover, row-active, row-active-hover, muted\n//\n// Usage example: background-color: color-variant($color-primary, hover);\n//\n\n// - - - - - - - - - START BOOTSTRAP COLOR DEFINITION - - - - - - - - - - - -\n\n//These gray colors defined in https://github.bmc.com/bmc-ux/adapt-angular/issues/576\n\n$white:     create-gray(#fff, 0%) !default;       //\n$gray-100:  create-gray(#fafafa, .5%) !default;   //.gray-100  {  background-color: #f9f9fa; }\n$gray-200:  create-gray(#f2f2f2, 1%) !default;    //.gray-200  {  background-color: #f0f0f1; }\n$gray-300:  create-gray(#d9d9d9, 1.5%) !default;  //.gray-300  {  background-color: #d6d7d8; }\n$gray-400:  create-gray(#b3b3b3, 2%) !default;    //.gray-400  {  background-color: #afb1b3; }\n$gray-500:  create-gray(#999, 2.5%) !default;     //.gray-500  {  background-color: #959799; }\n$gray-600:  create-gray(#808080, 3%) !default;    //.gray-600  {  background-color: #7c7f81; }\n$gray-700:  create-gray(#666, 3.5%) !default;     //.gray-700  {  background-color: #626668; }\n$gray-800:  create-gray(#515151, 4%) !default;    //.gray-800  {  background-color: #4e5154; }\n$gray-900:  create-gray(#333, 4.5%) !default;     //.gray-900  {  background-color: #313538; }\n$gray-1000: create-gray(#292929, 5%) !default;    //.gray-1000 {  background-color: #272b2f; }\n$black:     create-gray(#000, 0%) !default;       //\n\n// Sass scopes variables with if statement, so we need to define them first\n$theme-colors: null !default;\n\n// to meet accessible contrast ratio:\n@if ($accessible-mode) {\n  $theme-colors: (\n    primary: get-contrast-color($color-primary),\n    secondary: get-contrast-color($gray-700),\n    success: get-contrast-color($color-success),\n    info: get-contrast-color($color-info),\n    warning: get-contrast-color($color-warning, $black, false),\n    danger: get-contrast-color($color-critical),\n    light: get-contrast-color($white),\n    dark: get-contrast-color($gray-1000)\n  ) !default;\n} @else {\n  $theme-colors: (\n    primary: $color-primary,\n    secondary: $gray-700, //TODO: Consider alignment between this and our secondary color. Our secondary color is actually darker than our secondary color. Again a design consideration, but maybe should be aligned with color-secondary.\n    success: $color-success,\n    info: $color-info,\n    warning: $color-warning,\n    danger: $color-critical,\n    light: $white,\n    dark: $gray-900\n  ) !default;\n}\n\n// Set a specific jump point for requesting color jumps\n$theme-color-interval: 8% !default;\n\n// The yiq lightness value that determines when the lightness of color changes from \"dark\" to \"light\". Acceptable values are between 0 and 255.\n$yiq-contrasted-threshold: 150 !default;\n\n// Customize the light and dark text colors for use in our YIQ color contrast function.\n$yiq-text-dark: if($dark-mode, $gray-100, $gray-900) !default;\n$yiq-text-light: if($dark-mode, $gray-1000, $white) !default;\n\n// - - - - - - - - - END BOOTSTRAP COLOR DEFINITION - - - - - - - - - - - -\n\n\n// Text colors\n\n// https://github.bmc.com/bmc-ux/adapt-css/issues/638:\n\n$text-primary: if($accessible-mode, $black, $gray-900) !default;\n$text-primary-inverse: $white !default;\n\n$text-secondary: if($accessible-mode, $gray-900, $gray-700) !default;\n$text-secondary-inverse: $gray-200 !default;\n\n$text-tertiary: if($accessible-mode, $gray-700, $gray-500) !default;\n$text-tertiary-inverse: $gray-400 !default;\n\n// Colors used for all background and foreground colors\n$color-foreground: $white !default; // BMC default is white, #FFFFFF\n$color-background: $gray-200 !default; // BMC default is Off-white\n$color-foreground-inverse: $gray-900 !default; // BMC default is dark gray\n$color-background-inverse: $gray-1000 !default; // BMC default is near-black\n\n// Color ramp: Used for computing the other states on the base colors\n$hover: 6.5% !default;\n$active: $hover * 2.25 !default; // 14.625%\n$active-hover: $active * 1.5 !default; // 21.9375%\n$disabled: math.div($hov